import React, { Component } from "react";
import "materialize-css/dist/css/materialize.css";
class Total extends Component {
  empty() {
    if (this.props.amount === 0) {
      return null;
    } else {
      return (
        <li className="collection-item " key={this.props.id}>
          <p className="row">
            <b className="s12">{this.props.name}</b> x {this.props.amount}{" "}
            <i className="s12">Price: {this.props.price}KM</i>
          </p>
          <p className="chip center-align">Total: {this.props.total}KM</p>
        </li>
      );
    }
  }
  render() {
    return this.empty();
  }
}

export default Total;
