import React, { Component } from "react";
import "materialize-css/dist/css/materialize.css";
import "materialize-css/dist/js/materialize.js";
import "./item.css";

class Item extends Component {
  render() {
    let amount = this.props.amount;
    if (amount > 0) {
      return (
        <div className="relative">
          <div
            className="card hoverable"
            onClick={() => {
              this.props.onClick();
            }}
          >
            <div className="card-image">
              <img src={this.props.src} alt={this.props.alt} />

              <h5 className="card-title accent-1 blue-transparent">
                {this.props.name} <p>{this.props.price} KM</p>
              </h5>
            </div>
            <div className="card-content">
              <p>
                Total amount:
                <span className="">{this.props.amount}</span>
              </p>
            </div>
          </div>
          <button
            className="btn-floating btn-large red top-right waves-effect waves-light hoverable"
            onClick={() => {
              this.props.removeAmount();
            }}
          >
            X
          </button>
        </div>
      );
    } else {
      return (
        <div className="relative">
          <div
            className="card hoverable"
            onClick={() => {
              this.props.onClick();
            }}
          >
            <div className="card-image">
              <img src={this.props.src} alt={this.props.alt} />

              <h5 className="card-title accent-1 blue-transparent">
                {this.props.name} <p>{this.props.price} KM</p>
              </h5>
            </div>
            <div className="card-content">
              <p>
                Total amount: <span className="amount-badge">{this.props.amount}</span>{" "}
              </p>
            </div>
          </div>
        </div>
      );
    }
  }
}

export default Item;
