import React, { Component } from "react";
import Item from "./Item.jsx";
import Total from "./Total.jsx";
import "materialize-css/dist/css/materialize.css";

class Items extends Component {
  state = {
    items: [
      {
        id: 0,
        name: "coffe",
        price: 1.5,
        src: "./img/espresso.jpg",
        alt: "Cup of coffee",
        type: "hot-drinks",
        amount: 0,
        total: 0
      },
      {
        id: 1,
        name: "tea",
        price: 1.5,
        src: "./img/tea.jpg",
        alt: "Cup of tea",
        type: "hot-drinks",
        amount: 0,
        total: 0
      },
      {
        id: 2,
        name: "beer",
        price: 2.5,
        src: "./img/beer.jpg",
        alt: "Pint of beer",
        type: "beer",
        amount: 0,
        total: 0
      },
      {
        id: 3,
        name: "dark beer",
        price: 3.0,
        src: "./img/dark-beer.jpg",
        alt: "Glass of dark beer",
        type: "beer",
        amount: 0,
        total: 0
      },
      {
        id: 4,
        name: "Chocolate Pie",
        price: 3.5,
        src: "./img/ChocolateCream.jpg",
        alt: "Chocolate Pie",
        type: "cake",
        amount: 0,
        total: 0
      }
    ],
    id: 0
  };

  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
    /*this.addItem = this.addItem.bind(this);*/
    this.removeAmount = this.removeAmount.bind(this);
    this.checkOut = this.checkOut.bind(this);
  }
  handleClick(price, name, id) {
    let newState = this.state;
    newState.items[id].amount = this.state.items[id].amount + 1;
    newState.items[id].total = this.state.items[id].amount * this.state.items[id].price;
    newState.id = id;
    this.setState({ state: this.newState });
  }
  removeAmount(id) {
    let newAmount = this.state;
    if (newAmount.items[id].amount > 0) {
      newAmount.items[id].amount = this.state.items[id].amount - 1;
      newAmount.items[id].total =
        this.state.items[id].amount * this.state.items[id].price;
    }
    this.setState({ state: this.newAmount });
  }
  total() {
    let total = 0;
    for (let i = 0; i < this.state.items.length; i++) {
      total += this.state.items[i].total;
    }
    return total;
  }
  checkOut() {
    let totalZero = this.state.items;
    for (let i = 0; i < this.state.items.length; i++) {
      totalZero[i].amount = 0;
      totalZero[i].total = 0;
    }
    this.setState({ state: this.totalZero });
  }
  /*addItem(name, price, src, alt, type) {
    let newState = this.state;
    console.log(this.state);
    newState.items.push({
      id: newState.items.length,
      name: name,
      price: price,
      src: src,
      alt: alt,
      type: type,
      amount: 0
    });
    this.setState({ state: this.newState });
  }*/
  render() {
    return (
      <div className="row">
        {/*<div className="ml-3">
          <Login addItem={this.addItem} />
    </div>*/}

        <ul className="s8 m9 l10 col">
          <div className="row">
            {this.state.items.map(items => (
              <li className="col m6 l4 xl3 s12" key={items.id}>
                <Item
                  onClick={() => {
                    this.handleClick(items.price, items.name, items.id);
                  }}
                  removeAmount={() => {
                    this.removeAmount(items.id);
                  }}
                  name={items.name}
                  price={items.price}
                  amount={items.amount}
                  src={items.src}
                  alt={items.alt}
                />
              </li>
            ))}
          </div>
        </ul>
        <div className="s4 m3 l2 col sticky">
          <p className="blue-transparent card-panel">Order:</p>
          <ul className="collection">
            {this.state.items.map(items => (
              <Total
                key={this.state.items[items.id].id}
                id={this.state.items[items.id].id}
                name={this.state.items[items.id].name}
                amount={this.state.items[items.id].amount}
                price={this.state.items[items.id].price}
                total={
                  this.state.items[items.id].price * this.state.items[items.id].amount
                }
              />
            ))}
          </ul>
          <div className="blue-transparent card-panel right-align">
            <span>Total: {this.total()}KM </span>
            <button
              className="btn waves-effect waves-light check-out"
              onClick={this.checkOut}
            >
              Check Out
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default Items;
